document.querySelector('#result').onclick = () => {
  var box = document.getElementById('result');
  relType = box.checked?'w':'d';
  document.querySelector('#result ~ label').innerText = 'Relationships are ' 
    + (box.checked?'Wins':'Draws');
}
document.getElementById('addTeam').onclick=()=>{
  cy.add({
    group: "nodes",
    data: { id: '' + nextID, name: 'Page '+nextID },
    position: { x: (100 + nextID * 90 % 600), y: 100 }
  });
  nextID++;
  pageRank();
}
function pageRank() {
  var pr = cy.elements().pageRank({'dampingfactor': 0.85});
  window.pr = cy.elements();
  cy.nodes().forEach( ele => {
    var nodeRank = pr.rank(ele);
    ele.style({'content': 'Page ' + ele.id() + '\n' + Math.round(nodeRank * 1000) / 1000 });
  });
  cy.nodes().flashClass('nolabel',1);
}
function deleteSelected() {
  var selection = cy.$(':selected');
  var edgesToRemove = selection.connectedEdges();
  edgesToRemove.remove();
  selection.remove();
  cy.selectedNode = null;
  cy.elements().removeClass('faded');
  pageRank();
}
document.getElementById('runPageRank').onclick=pageRank;
document.getElementById('delete').onclick=deleteSelected;
